import React from 'react';
import * as UI from './styles';
import avatar from '../../../assets/images/avatar.jpg';

const Header = () => {
  
  return (
    <UI.HeaderWrapper>
      <UI.Avatar src={avatar} />
      <UI.Text>My first name is <b>Nikita</b>, my last name is <b>Iskorkin</b>.</UI.Text>
      <UI.Text>19 y.o. developer from Perm.</UI.Text>
    </UI.HeaderWrapper>
  );
};

export default Header;