import React from 'react';
import * as UI from './styles';

const Plate = (props) => {
  const { title, description, link, target } = props;

  const linkProps = {
    href: link,
    target: target
  }

  return <UI.Wrapper {...linkProps}>
    <UI.Title>{title}</UI.Title>
    {description}
  </UI.Wrapper>
};

export default Plate;
